
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #ebedf1;
  /*margin-top: 60px;*/
  height: 100vh;
  background:  #1f1b1b;
  border:2px solid #9f729d;
}
.home{
  background: #1f1b1b;
}
.characters-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.character-block {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  width: 24%;
  margin: 15px;
  background: #403f50;
}
.character-block, .character-block__image img{
  width: 200px;
  height: auto;
  border-radius: 5px;
}
.character-block,  .character-block__info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  font-size: 14px;
}
.character-block,  .character-block__info span{
  margin: 5px;
  /*color: #57566c;*/
}
.character-name-info {
  color: #19b27b;
  font-size: 20px;
}
.character-location-name-info{
  color: #19b27b;
}
.character-name-info:hover, .character-location-name-info:hover{
  cursor: pointer;
  color: #ffae00;
}
.character-name-span{
  color: #9f9fc4;
  font-size: 14px;
}
.characters-btn{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: relative;
  background: #19b27b;
  border-radius: 5px ;
  width:120px;
  left: 2%;
  padding: 4px;
  margin: 10px;
  -webkit-box-shadow: 0 3px 10px rgb(143, 33, 49), inset 0 1px 0 rgba(255, 255, 255, 0.42);
          box-shadow: 0 3px 10px rgb(143, 33, 49), inset 0 1px 0 rgba(255, 255, 255, 0.42);
  -webkit-transition: .3s linear all;
  transition: .3s linear all;
}
.characters-btn:hover{
  cursor: pointer;
  width:130px;
  height: 29px;
  margin-bottom: 8px;
  background: darkgray;
  color: #01573a;
  -webkit-box-shadow: 0 3px 10px rgb(25, 178, 123), inset 0 1px 0 rgba(255, 255, 255, 0.42);
          box-shadow: 0 3px 10px rgb(25, 178, 123), inset 0 1px 0 rgba(255, 255, 255, 0.42);
}


